@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: #fff;
    color: #515151;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

.wrapper{
    width: 90%;
    margin: 50px auto;
}

header{
    position: relative;
}
header .logo{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav{
    float: right;
    list-style: none;
}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms ease;
    background: #515151;
    padding: 10px;
    border-radius: 15px;

}
header ul.nav li a{
    text-decoration: none;
    color: white;
}
header ul.nav li:hover{
    opacity: 0.5;

}

header .shop-cart-button{
    float: right;
    cursor: pointer;
    transition: color, transform 200ms ease;
    font-size: 20px;
}
header .shop-cart-button:hover,
header .shop-cart-button.active{
    color: #dc3d3c;
    transform: scale(1.2);
    
}

header .shop-cart{
    position: absolute;
    top: 30px;
    right: 0;
    width: 450px;
    background: #fafafa;
    z-index: 1000;

    padding: 20px;

}

header .shop-cart .empty h2{
    font-size: 20px;
}

header .shop-cart .item{
    width: 100%;
    float: left;
    margin: 20px;
}

header .shop-cart .item img{
    width: 90%;
    float: left;
 
}
header .shop-cart .item h2{
    font-size: 20px;
    margin-bottom: 10px;
}
header .shop-cart .item p{
    color: #515151;
    font-weight: 600;
}

header .shop-cart .item .delete-icon{
    color: #ca5252;
    float: right;
    position: relative;
    top: -20px;
    cursor: pointer;
    margin-right: 45px;
    transition: color, transform 200ms ease;
    font-size: 25px;
}
header .shop-cart .item .delete-icon:hover {
    color: #515151;
    transform: scale(1.2);
}

header .shop-cart .summa{
    margin-left: 10px;
    font-size: 17px;
    font-weight: 600;
}

header .presentation{
    margin: 50px 0;
    background: url(img/room1.jpg) no-repeat;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: #ffffff;
    position: relative;
}

header .presentation::after{
    content: 'Найкращі дизайни у Львові';
    position: absolute;
    top: 100px;
    left: 50px;
    width: 460px;
    font-size: 40px;
    font-weight: 600;
    color: #fff; 
}

header .presentation::before{
    content: 'все для вашого щастя';
    position: absolute;
    top: 210px;
    left: 50px;
    width: 460px;
    font-size: 25px;
    font-weight: 300;
    color: #fff;
}

.categories div{


    border-radius: 10px;
    padding: 10px, 20px;
    margin-bottom: 15px;
    margin-right: 15px;
    margin-left: 10%;
    padding: 10px;
    cursor: pointer;
    font-size: 20px;

    transition: all 200ms ease;
}
.categories h1{

    border-radius: 10px;
    padding: 10px, 20px;
    margin-bottom: 15px;
    margin-right: 15px;
    margin-left: 10%;
    padding: 10px;
    font-size: 30px;
}
.categories{
    float: left;
    width: 24%;
}

.categories div:hover{

    background-color: #e9e9e9;
}

.full-item{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #51515149;
    z-index: 9999;
    overflow: auto;
}

.full-item > div{
    width: 600px;
    position: relative;
    margin: 10% auto;
    padding: 40px 30px;
    background: white;
    border-radius: 10px;
}

main {
    display: flex;
    float: right;
    width: 75%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item{
    width: 30%;
    margin-bottom: 50px;
    background: #f1f1f1;
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    position: relative;
    padding-bottom: 20px;

}

main .item img , .full-item img{
    width: 100%;
    border-radius: 10px 10px 0 0;
    transition: transform 200ms ease;
    cursor: pointer;
}
main .item img:hover, 
.full-item img:hover{
    transform: scale(1.05);
}

main h2, .full-item h2,
main p, .full-item p,
main b,
.full-item b{
    margin: 10px 20px;
    color: #333;
} 
main b,
.full-item b{
    color: #55b865;
} 
main .add-to-cart,
.full-item .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: white;
    border-radius: 50%;
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    transition: transform 200ms ease;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover{
    transform: scale(1.2);

}

.map iframe{
    width: 100%;
    border-radius: 25px;
    border: #515151 solid 5px;
}
.map{
    width: 90%;
    margin: auto;

}
.map h1{
    margin-top: 150px;
    padding: 15px;
}

.description{
    width: 90%;
    margin: auto;
}
.description h1{

    padding-top: 40px;
    font-size: 40px;

}
.description h2{

    padding-top: 40px;
    font-size: 20px;
    font-weight: 300;
}

.statistic{
    padding-left: 50px;
    padding-top: 90px;
}
.statistic h1{
    color: #ffa600;
    font-size: 70px;
    
}
.statistic h2{
    font-weight: 300;
}
.statistic .statistic-block-1{
    width: 45%;
    display: inline-block;
    padding: 30px;
}

form{
    background: #ffa600;

    padding: 2vw 0vw;
    width: 65%;

}
.form-row .input-block-1{
    width: 50%;
    display: inline-block;
    text-align: center;
    padding-top: 25px;
}
.container-form h1{
    text-align: center;
    color: #694501;
    font-size: 45px;
}
.textarea-block-1{
    width: 100%;
    display: inline-block;
    text-align: center;
    padding-top: 25px;
}
.textarea-block-1 textarea{
    width: 65.5%;
}

form input, form textarea{
    border: 0;
    padding: 30px;
    font-size: 16px;
}
.down-line{
    align-items: center;
    text-align: center;
}
form button{
    padding:  25px;
    background: #694501;
    color: #fff;
    font-size: 25px;
    border: 0;
    outline: none;
    cursor: pointer;
    width: 200px;
    margin: 20px auto 0;

}
form button:hover{
    background: #a56b00;

}
.container-form{
    

    display: flex;
    justify-content: center;
    padding-bottom: 45px;
    
}
.conteiner{
    text-align: center;
    align-items: center;
}
.info{
    text-align: center;
    padding-top: 50px;
    padding-bottom: 150px;
}
.info h1{
    font-weight: 300;
    font-size: 60px;
}
.info h2{
    font-weight: 300;
    font-size: 45px;
}
.form-text{
    text-align: center;
    padding-bottom: 15px;
}
footer{
    text-align: center;
    position: fixed; 
    left: 0; bottom: 0; 
    
}
